html,
body {
  padding: 0;
  margin: 0;
}

h1 {
  font-size: 3em;
  border-bottom: 1px dashed #000;
  padding-bottom: 2rem;
  margin: 0 0 2rem 0;
}

.wrapper {
  max-width: 800px;
  width: 100%;
  text-align: center;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100vh;
}

.row {
  width: 500px;
}

form {
  display: flex;
  border-bottom: 1px solid #000;
  padding-bottom: 2rem;
}
form input {
  padding: 10px;
  background-color: #eee;
  border: 0;
  width: 100%;
  font-size: 18px;
}
form input:focus {
  outline: 2px solid #000;
}

form button {
  cursor: pointer;
  border: 0;
  background-color: #000;
  width: 120px;
  color: #fff;
  padding: 10px;
  transition: all 0.5s ease-in-out;
}
form button:hover {
  background-color: rgb(90, 90, 90);
}

/* todo list */
ul {
  list-style: none;
  text-align: left;
  padding: 2rem;
  margin: 0;
}
ul li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 18px;
  background-color: #eee;
  padding: 8px;
}
ul li:not(:last-child) {
  margin-bottom: 8px;
}
ul li p {
  margin: 0;
}
ul li button {
  cursor: pointer;
  display: flex;
  background-color: #000;
  font-size: 2em;
  border: 1px solid #fff;
  color: #fff;
  transition: all 0.5s ease-in-out;
}
ul li button:hover {
  background-color: #fff;
  border: 1px solid #000;
  color: #000;
}
